//

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

//STYLE: MANUZIO

//pc view
.reader.manuzio {
  article {
    padding: 18% 34.5% 30% 12%;
    background-image: url("./styles/first/background.jpg");
    background-repeat: no-repeat;
    background-position: 50%;
    background-attachment: fixed;
    background-size: cover;
    box-sizing: border-box;
    font-family: $manuzio-font;
    font-style: normal;
    font-weight: 400;
    line-height: 1.3;
    color: $manuzio-text-color;
    font-size: 1.9rem;
    text-align: justify;
    

    section {
      margin: 2rem 0;
p:first-of-type:first-letter {
        text-transform: uppercase;
        float: left;
        color: $manuzio-first-letter;
        font-size: 13rem;
        line-height: 10rem;
        text-align: left;
        margin: 1rem 0.5rem 0.5rem 0;
        font-family: $manuzio-font-initial;
      }
      
    }

    a:-webkit-any-link {
      color: $manuzio-link-color;
    }
    p {
      margin: 1rem 0;
    }
    figure {
      text-align: center;
      margin: 1rem 0;
      figcaption {
        font-family: $manuzio-font-maiusc;
        font-style: normal;
        font-size: 1.7rem;
        &::first-child {
          margin: 0;
        }
      }
      img {
        height: auto;
        width: 100%;
        object-fit: contain;
        margin: 0.5rem 0;
        box-align: center;
        -webkit-filter: grayscale(100%) sepia(0.2) contrast(100%);
        filter: grayscale(100%) sepia(0.2) contrast(100%);
        border-width: 0.3rem;
        border-style: double;

        &:hover {
          -webkit-filter: none;
        }

        &:first-of-type {
          margin-bottom: 1rem 0;
        }
      }
    }
    table {
      table-layout: fixed;
      width: 100%;
      border-width: 0.3rem;
      border-style: double;
      margin: 0.2rem 0;
      border-collapse: collapse;
      text-align: center;
      line-height: 1.5rem;
      font-size: 1.5rem;
    }
    th {
      text-transform: uppercase;
      font-weight: 600;
      text-align: center;
      border: 0.1rem solid $manuzio-text-color;
      padding: 0.5rem;
      font-style: normal;
    }
    td {
      padding: 0.5rem;
      border: 0.1rem solid $manuzio-text-color;
    }
    h1,
    h2,
    h3,
    h4 {
      font-family: $manuzio-font-maiusc;
      font-size: 2rem;
      text-transform: uppercase;
      font-weight: 600;
      letter-spacing: 0.3rem;
      margin: 2rem 0;
      font-style: normal;
    }
    main {
      h1,
      h2,
      h3,
      h4 {
        text-align: center;
      }
      
    }
    header {
      text-align: center;
      font-family: $manuzio-font-maiusc;
      font-weight: 500;
      h1:after {
        content: "";
        display: inline-block;
        background-image: url(./styles/first/manuzio-logo.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        width: 100%;
        padding-top: 100%;
        // margin: 3rem 0 3rem 0;
      }
      &:after {
        content: "";
        display: inline-block;
        background-image: url(./styles/first/decoration.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 110%;
        width: 100%;
        padding-top: 30%;
      }
    }
    footer {
      margin: 3rem 0 0 0;
      text-align: center;
      font-family: $manuzio-font-maiusc;
    }
  }
  //EU article
  .left,
  .right {
    article {
      font-size: 1.4rem;
      h1, h2, h3, h4 {
        font-size: 1.6rem;
      }
      main {
      p:first-of-type:first-letter {
        font-size: 10rem;
        line-height: 6rem;
      }
    }
    }
  }
  .left {
    article{
      padding: 9% 6% 15% 17.2%;
      
    }
  }
  .right {
    article {
      padding: 9% 17.2% 15% 6%;
    }
  }
}

//triangles
.left-triangle-shape {
  float: left;
  shape-outside: polygon(0 0, 0 100%, 100% 50%);
  clip-path: polygon(0 0, 0 100%, 100% 50%);
  width: 45%;
  position: relative;
}
.right-triangle-shape {
  float: right;
  shape-outside: polygon(100% 0, 100% 100%, 0 50%);
  clip-path: polygon(100% 0, 100% 100%, 0 50%);
  width: 45%;
  position: relative;
  
}
//mobile-portrait
@media (min-width: 320px) and (max-width: 650px) {
.reader.rimpa {
  article {
    font-size: small;
    h1, h2, h3, h4 {
      font-size: medium;
    }
    p:first-of-type:first-letter {
      font-size: 6rem;
      line-height: 3rem;
      text-align: left;
    }
  }
  .left,
  .right {
    webkit-animation: slide-in-top 0.5s both;
	  animation: slide-in-top 0.5s both;
    article {
      padding: 9%, 17.2%, 15%, 6%;
    }
  }
}
}

//STYLE: RIMPA

.reader.rimpa {
  width: 100%;
  article {
    padding: 5%;
    background-image: url("./styles/second/background.jpg");
    background-repeat: no-repeat;
    background-position: 50%;
    background-attachment: fixed;
    background-size: cover;
    box-sizing: border-box;
    font-family: $rimpa-font;
    font-weight: 400;
    line-height: 1.7;
    color: $rimpa-text-color;
    font-size: 1.5rem;
    overflow-x: auto;
    overflow-y: auto;


    section {
      margin: 1rem;
    }

    a:-webkit-any-link {
      color: #777;
    }
    p {
      padding: 0.5rem 0;
    }
    figure {
      padding: 0 1rem;
      

      figcaption {
        font-style: italic;
        font-size: smaller;
        padding: 1rem;
      }
      img {
        height: auto;
        width: 70vh;
        object-fit: contain;
        display: block;
        margin-left: auto;
        margin-right: auto;
        box-align: center;
        
      }
      &:hover {
        -webkit-filter: none;
      }
      &:first-of-type {
        margin-bottom: 1rem;
      }
    }
    .image-background-color {
      background: $rimpa-yellow;
      height: fit-content;
      width: fit-content;
      img {
        -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  opacity: 0.5;
      }
  }
    table {
      table-layout: fixed;
      width: 100%;
      border-width: 0.3rem;
      padding: 0.2rem;
      border-collapse: collapse;
      background: rgba(234, 197, 67, 0.5);
    }
    th {
      font-size: 1rem;
      text-transform: uppercase;
      font-weight: 600;
      text-align: center;
      border: 0.1rem solid $rimpa-text-color;
      padding: 0.2rem;
      font-style: normal;
    }
    td {
      padding: 0.2rem;
      border: 0.1rem solid $rimpa-text-color;
    }
    p,
    h1,
    h2,
    h3,
    h4 {
      padding: 0 1rem;
      background: -webkit-linear-gradient(
        left,
        $rimpa-text-color,
        $rimpa-light-gradient
      );
      background: -o-linear-gradient(
        right,
        $rimpa-text-color,
        $rimpa-light-gradient
      );
      background: -moz-linear-gradient(
        right,
        $rimpa-text-color,
        $rimpa-light-gradient
      );
      background: linear-gradient(
        to right,
        $rimpa-text-color,
        $rimpa-light-gradient
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    h1,
    h2,
    h3,
    h4 {
      font-size: 2.2rem;
      text-transform: uppercase;
      font-weight: 600;
      letter-spacing: 0.3rem;
      // padding: 2rem 0;
      
    }
     main {
      h1,
      h2,
      h3,
      h4 {
        text-align: center;
      }
      h2 {
        font-size: 2.5rem;
        font-weight: 500;
        letter-spacing: 0.2rem;
      }
      h3 {
        font-size: 2rem;
        font-weight: 500;
        padding: 1rem 0;
      }
      h3 {
        font-size: 1.5rem;
        font-weight: 500;
        padding: 1rem 0;
      }
    }
    header {
      text-transform: uppercase;
      writing-mode: vertical-lr;
      text-orientation: upright;
    }
    footer {
      margin: 1rem;
      text-transform: uppercase;
      height: 100vh;
    }
    
  }
//EU article
.left,
.right {
  article {
    font-size: 1.4rem;
    h1, h2, h3, h4 {
      font-size: 1.6rem;
    }
  }
  }
}

//STYLE: JAN TSCHICHOLD

.reader.third {
  .reader.left {
    article {
      main {
        //padding: 5rem;
        margin: 2rem 4rem 6rem 2rem;
        background-color: #f6f3ea;
        text-align: justify;
        column-count: 1;
      }
    }
  }

  .reader.right {
    article {
      main {
        //padding: 5rem;
        margin: 2rem 4rem 6rem 2rem;
        background-color: #f6f3ea;
        text-align: justify;
        column-count: 1;
      }
    }
  }

  article {
    background-color: #f6f3ea;
    /*font-family: 'Roboto', sans-serif;*/
    font-family: $font-futura;
    /*font-family: 'Futura W01', helvetica, sans-serif;*/
    font-size: 100%;
    color: rgba(12, 12, 10, 0.8);
    font-weight: 300;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;

    header {
      width: 55vw;
      padding-top: 14vh;
      //min-height: 100vh;
      //padding: 1rem 1vw 1rem;
      display: grid;
      margin: auto;
      grid-template-columns: auto 1fr 1fr 1fr 0.5fr;
      grid-template-rows: auto 1fr 1fr auto auto;
      grid-gap: 1rem;
      transform: rotate(-30deg);

      h1,
      h3 {
        font-size: calc(1rem + 1.4vw);
        margin: 0;
        font-weight: 700;
        line-height: 1.2em;
      }

      h1 {
        /* autoprefixer: off */
        color: #f9423a;
        -webkit-writing-mode: vertical-rl;
        writing-mode: vertical-rl;
        grid-column: 3 / 4;
        grid-row: 2 / 5;
        justify-self: end;
        margin-top: -10px;
        margin-right: -10px;
      }

      h3 {
        grid-column: 1 / 4;
        grid-row: 1 / 2; //
        text-align: right;
        align-self: end; //
        font-size: 1.3rem;
        border-bottom: 15px solid #f9423a;

        &::first-letter {
          color: #0077c0;
          font-size: calc(3rem + 2vw);
          font-family: $font-transito;
        }
      }

      h4 {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        text-align: right;
        font-weight: 700;
        text-transform: uppercase;

        &::first-letter {
          color: #0077c0;
          font-size: calc(2rem + 2vw);
          font-family: $font-transito;
        }
      }

      p {
        margin: 0;
        grid-column: 4 / 5;
        grid-row: 2 / 3;
        color: rgba(12, 12, 10, 0.8);
        border-bottom: none;
        text-transform: uppercase;
        color: rgba(12, 12, 10, 0.8);
        font-weight: 700;

        &:first-of-type {
          /* autoprefixer: off */
          border-top: 5px solid rgba(12, 12, 10, 0.8);
          border-bottom: 5px solid rgba(12, 12, 10, 0.8);
          font-weight: 700;
          grid-row: 3;
          grid-column: 4 / 6;
          align-self: start;
        }
      }

      tr {
        grid-auto-flow: column;

        td {
          width: 20px;
        }
      }

      img {
        width: 80%;
        height: auto;
        object-fit: contain;
        display: block;
        margin-left: auto;
        margin-right: auto;
        clip-path: circle();
        -webkit-filter: grayscale(100%) contrast(100%);
        filter: grayscale(100%) contrast(100%);
      }

      figure {
        //grid-column: 4 / 6;
        //grid-row: 1 / 3;

        img {
          //works only in the header
          width: 80%;
          //width: 200%; //
          //margin-left: -50px;
          height: auto;
          object-fit: contain;
          display: block;
          margin-left: auto;
          margin-right: auto;
          clip-path: circle();
          -webkit-filter: grayscale(100%) contrast(100%);
          filter: grayscale(100%) contrast(100%);
        }

        figcaption {
          width: 200px;
          padding: 3px;
          margin-left: auto;
          margin-right: auto;
          text-align: center;
          border-left: 5px solid rgba(12, 12, 10, 0.8);
          border-right: 5px solid rgba(12, 12, 10, 0.8);
        }
      }
    }

    main {
      //padding: 5rem;
      margin: 2rem 4rem 6rem 2rem;
      background-color: #f6f3ea;
      text-align: justify;
      column-count: 2;
      column-gap: 4vw;

      p {
        font-size: 1rem;
      }

      figure {
        width: auto;

        img {
          width: 100%;
          height: auto;
          object-fit: contain;
          margin-top: 2%;
          display: block;
          //margin-left: auto;
          //margin-right: auto;
          -webkit-filter: grayscale(100%) contrast(100%);
          filter: grayscale(100%) contrast(100%);
        }

        figcaption {
          font-style: $font-futura-black;
          font-weight: 700;
        }
      }
    }

    h1,
    h2,
    h3 {
      margin: 0;
      text-transform: uppercase;
    }

    footer {
      background-color: #f6f3ea;
      color: rgba(12, 12, 10, 0.8);
      line-height: 1.2rem;
      border-top: 15px solid #f9423a;
      border-bottom: 15px solid #f9423a;
      text-align: center;
      font-style: $font-futura;
    }
  }

  /*header p {
        margin: 2rem 0 0; }*/

  /* article > header {
        display: flex;
        margin: auto;
        padding-top: 1rem;
        padding-bottom: 1rem; } */

  /*@media (min-width: 1800px) {
          article > header {
          padding-bottom: 30rem; } }*/
}

